import csstype.*
import emotion.react.css
import kotlinx.js.jso
import react.FC
import react.Props
import react.dom.html.ReactHTML
import react.useState

val App = FC<Props> {
    var clicked by useState<Boolean>(false)
    var activeInd by useState<Int>(0)


    ReactHTML.button {
        +"Show"
        onClick = {
            clicked = !clicked
        }
    }
    ReactHTML.div {
        className = ClassName("sidebar")
        ReactHTML.a {
            href = "#"
            className = ClassName(if(activeInd == 0) "active" else "")
            onClick = {
                activeInd = 0
            }
            ReactHTML.i {
                className = ClassName("fas fa-band-aid")
                style = jso {
                    fontSize = 40.px
                }
            }
            +"News"
            css {
                display= Display.block
                color= NamedColor.black
                padding = 16.px
                textDecoration = None.none
                if(activeInd == 0) {
                    backgroundColor= Color("#04AA6D")
                    color = NamedColor.white
                } else {
                    hover {
                        backgroundColor= Color("#555")
                        color = NamedColor.white
                    }
                }
            }
        }
        ReactHTML.a {
            href = "#"
            className = ClassName(if(activeInd == 1) "active" else "")
            onClick = {
                activeInd = 1
            }
            +"Content"

            css {
                display= Display.block
                color= NamedColor.black
                padding = 16.px
                textDecoration = None.none
                if(activeInd == 1) {
                    backgroundColor= Color("#04AA6D")
                    color = NamedColor.white
                } else {
                    hover {
                        backgroundColor= Color("#555")
                        color = NamedColor.white
                    }
                }
            }
        }
        ReactHTML.a {
            href = "#"
            className = ClassName(if(activeInd == 2) "active" else "")
            onClick = {
                activeInd = 2
            }
            +"About"

            css {
                display= Display.block
                color= NamedColor.black
                padding = 16.px
                textDecoration = None.none
                if(activeInd == 2) {
                    backgroundColor= Color("#04AA6D")
                    color = NamedColor.white
                } else {
                    hover {
                        backgroundColor= Color("#555")
                        color = NamedColor.white
                    }
                }
            }
        }
        css {
            margin = 0.px
            padding = 0.px
            width = 200.px
            backgroundColor = Color("#f1f1f1")
            position= Position.fixed
            height= 100.vh
            overflow = Overflow.clip
            display = if(clicked) Display.block else None.none
        }


    }
}